<template>
  <div id="app">
    <div class="red-box">
      <div class="yellow-box">
        <p class="title">Card Memory Game for Blue Archives</p>
        <div class="header">
          <el-input v-model="cardNumber" placeholder="请输入卡牌组数" style="width: 100px"></el-input>
          <el-button @click="randomlyCards" type="primary" round :disabled="isbutton">生成牌组</el-button>
          <el-button @click="refreshPage" type="danger" round>再玩一把?</el-button>
          <span v-if="nowTime">剩余时间:{{ nowTime }}</span>
          <span v-else>剩余时间:{{ tentativeHours }}:{{ tentativeMin }}:00</span>
        </div>
        <div class="generated-box">
          <a href="#" @click.prevent="Flipping(index, item.content)" v-for="(item, index) in Deck" :key="index">
            <el-card :class="{ addBg: item.isActive, boxCard: true }">
              <img v-if="item.src" :src="item.src" :key="index" v-lazy="item.src" :class="{ active: item.isPlay }" />
            </el-card>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      flag: 0,
      isbutton: false,
      isPlay: false,
      isAdd: true,
      status: true,
      isAnimating: false,
      Deck: [],
      DuckTwo: [],
      firNum: '',
      secNum: '',
      firContent: '',
      secContent: '',
      cardNumber: 5,
      lastTime: 0,
      setMin: 1,
      setHours: 0,
      Timer: null
    }
  },
  computed: {
    nowTime() {
      if (this.lastTime === 0) {
        return false
      } else {
        let H = Math.floor(this.lastTime / 3600)
        let M = Math.floor(this.lastTime / 60) % 60
        let S = this.lastTime % 60
        if (H < 10) H = `0${H}`
        if (M < 10) M = `0${M}`
        if (S < 10) S = `0${S}`
        return `${H}:${M}:${S}`
      }
    },
    tentativeMin() {
      if (this.setMin < 10) {
        return `0${this.setMin}`
      }
    },
    tentativeHours() {
      if (this.setHours < 10) {
        return `0${this.setHours}`
      }
    }
  },
  methods: {
    refreshPage() {
      window.location.reload() // 刷新页面
    },
    setTime() {
      if (this.status === true) {
        this.lastTime = this.setHours * 3600 + this.setMin * 60
        this.status = false
      }
      // console.log(this.lastTime / 3600)
      // console.log(Math.floor(this.lastTime / 3600))

      if (this.lastTime > 0) {
        this.lastTime--
      } else {
        clearInterval(this.Timer)
        return alert('倒计时结束')
      }
    },
    Flipping(index, content) {
      if (this.flag === 0) {
        this.$set(this.Deck[index], 'isPlay', !this.Deck[index].isPlay)
        this.firNum = index
        this.firContent = content
        this.flag = 1
        console.log(index)
      } else if (this.flag === 1 && !this.isAnimating) {
        this.isAnimating = true // 设置isAnimating为true，表示正在执行翻转动画
        this.$set(this.Deck[index], 'isPlay', !this.Deck[index].isPlay)
        this.secNum = index
        this.secContent = content
        this.determine()
      }
    },
    determine() {
      setTimeout(() => {
        if (this.firContent === this.secContent) {
          // 消掉这两个卡牌
          this.$set(this.Deck[this.firNum], 'isPlay', !this.Deck[this.firNum].isPlay)
          this.$set(this.Deck[this.secNum], 'isPlay', !this.Deck[this.secNum].isPlay)
          console.log(true)
          this.$set(this.Deck[this.firNum], 'isActive', !this.Deck[this.firNum].isActive)
          this.$set(this.Deck[this.secNum], 'isActive', !this.Deck[this.secNum].isActive)
          this.flag = 0
        } else {
          //不一样,翻回去
          this.$set(this.Deck[this.firNum], 'isPlay', !this.Deck[this.firNum].isPlay)
          this.$set(this.Deck[this.secNum], 'isPlay', !this.Deck[this.secNum].isPlay)
          console.log(false)
          this.flag = 0
        }
        this.isAnimating = false // 动画执行完成后，将isAnimating重置为false
      }, 500)
    },
    randomlyCards() {
      this.Timer = setInterval(() => {
        this.setTime()
      }, 1000)
      this.isbutton = true
      for (let i = 0; i < this.cardNumber; i++) {
        let count = Math.floor(Math.random() * (9 - 2 + 1) + 2)
        let obj = {
          src: require(`@/assets/img/${count}.png`),
          content: count,
          isActive: false
        }
        this.Deck.push(obj)
      }
      // console.log(this.Deck)
      this.shuffle()
    },
    // 打乱牌序
    shuffle() {
      let array = JSON.parse(JSON.stringify(this.Deck))
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1))
        ;[array[i], array[j]] = [array[j], array[i]]
      }
      this.DuckTwo = array
      console.log(this.DuckTwo)
      this.Deck.push(...this.DuckTwo)
      console.log(this.Deck)
    }
  }
}
</script>

